import React, { Component } from 'react';
import './OrderLabel.css';
import changeCase from 'change-case'

class OrderLabel extends Component {
  render() {
    var classList = ""
    if ( this.props.isWhatsapp ) {
      classList += "whatsapp"
    }
    if ( this.props.isEmail ) {
      classList += "email"
    }
    if ( this.props.isArabic ) {
      classList += " arabic"
    }
    if ( this.props.isPayment ) {
      classList = "payment-" + this.props.paymentType + " " + changeCase.paramCase(this.props.tagLabel)
    }
    if ( this.props.isWhatsapp || this.props.isEmail || this.props.isPayment ) {
      classList += " has-tag"
    }
    return (
      <div className={'order-label ' + changeCase.paramCase(this.props.labelTitle)}>
      <span className="label-title">{changeCase.titleCase(this.props.labelTitle)}</span>
        <div className={classList}>
          <span className="label-content" dangerouslySetInnerHTML={{__html: this.props.labelContent}}></span>
          <a className="label-tag" href={this.props.tagLink}>{this.props.tagLabel}</a>
        </div>
      </div>
    );
  }
}

export default OrderLabel;



