import React, { Component } from 'react';
const style = {
  textAlign: 'center',
fontSize: '24px',
color: 'rgba(0,0,0,0.37)',
fontWeight: '300',
maxWidth: '700px',
fontFamily: 'Roboto',
width: '100%',
lineHeight: '1.8',
margin: '128px auto'
}
class EmptyTab extends Component {
  render() {
    return(
      <div className="emptyContainer">
        <h1 style={style} >Orders will be shown here, this section is empty for now, when new orders of this type are added, this will get filled</h1>
      </div>
    )
  }
}

export default EmptyTab;



