import React, { Component } from 'react';
import OrderLabel from './OrderLabel'
import './LabelGroup.css';

class LabelGroup extends Component {
  render() {
    var labels = []
    this.props.subGroup.map(function(elem, index ){
      return labels.push(<OrderLabel
       key={index}
       labelTitle={elem.labelTitle}
       labelContent={elem.labelContent}
       isArabic={elem.isArabic}
       isWhatsapp={elem.isWhatsapp}
       isEmail={elem.isEmail}
       isPayment={elem.isPayment}
       paymentType={elem.paymentType}
       tagLink={elem.tagLink}
       tagLabel={elem.tagLabel}
     />)
    })
        return (
          <div className='labelGroup'>
              {labels}
          </div>
        );
  }
}
export default LabelGroup;



