import React, { Component } from 'react';
import OrderButton from './OrderButton';
import RaisedButton from 'material-ui/RaisedButton'
import Dialog from 'material-ui/Dialog'
import ajax from '@fdaciuk/ajax'

const style = {
  button : {
    margin: '12px'
  }
}
class OrderActions extends Component {
  state = {
   open: false,
   };

   handleOpen = () => {
     this.setState({open: true});
   };

   handleClose = () => {
     this.setState({open: false});
   };

   handleRemove = () => {
     /* global apiLink */
     /* global nonce */
     var ajaxLink = apiLink + 'order/' + this.props.orderId
     ajax({
     method: 'delete',
     headers : {
       'X-WP-Nonce' : nonce
     },
     url: ajaxLink
     }).then(function(res) {
       this.props.visibility()
       this.handleClose()
     }.bind(this))
   }
  render() {
    const actions = [
      <RaisedButton
        label="Cancel"
        primary={false}
        keyboardFocused={true}
        onTouchTap={this.handleClose}
      />,
      <RaisedButton
        label="Remove"
        secondary={true}
        style={style.button}
        onTouchTap={this.handleRemove}
      />,
    ];
    var labels = [];
    var buttons = [];
    if (this.props.orderStatus === 'new') {
      labels = ['paid', 'delivered']
    } else if (this.props.orderStatus === 'paid') {
      labels = ['unpaid', 'delivered']
    } else if (this.props.orderStatus === 'delivered') {
      labels = ['unpaid', 'undelivered']
    } else {
      labels = ['paid', 'delivered']
    }
    labels.map(function(value, key) {
      return buttons.push(<OrderButton label={value} key={key} style={style.button} id={this.props.orderId} actionType={value} className={value}  />);
    }.bind(this))
    return (
      <div className="order-actions">
        {buttons}
        <RaisedButton label="remove" style={style.button}  className="remove" secondary={true} onTouchTap={this.handleOpen} />
          <Dialog
              title="Remove an order"
              actions={actions}
              modal={false}
              open={this.state.open}
              onRequestClose={this.handleClose}
              orderId={this.props.orderId}
            >
              Are You sure you want to delete order number {this.props.orderId} ?
            </Dialog>
      </div>
    )
  }
}

export default OrderActions



