import React, { Component } from 'react';
import './Order.css';
import Paper from 'material-ui/Paper';
import LabelGroup from './LabelGroup';
import chunk from 'lodash/chunk';
import values from 'lodash/values';
import OrderActions from './OrderActions';

const style = {
  labelContainer : {
    width: '100%',
    height: '100%',
    display: 'flex',
    justifyContent: 'space-between'
  }
};



class Order extends Component {
  state = {
   visible: 'visible',
   };
   setVisibility = () => {
     this.setState({visible: 'hidden'});
   };
  render() {
    var labels = values(this.props.order)
    labels = chunk(labels, 4)
    var groups = []
    labels.map(function(subGroup, index ){
      return groups.push(<LabelGroup key={index} subGroup={subGroup} />)
    })
    return (
      <div className={"Order order-is-" + this.state.visible} data-id={this.props.orderId}>
      <Paper zDepth={1} >
        <div style={style.labelContainer} className="order-labels">
          {groups}
        </div>
        <OrderActions visibility={this.setVisibility} orderStatus={this.props.orderStatus} orderId={this.props.orderId} />
      </Paper>
      </div>
    );
  }
}

export default Order;



