import React, { Component } from 'react';
import RaisedButton from 'material-ui/RaisedButton'
import ajax from '@fdaciuk/ajax'

class OrderButton extends Component {
  /*global apiLink*/
  /*global nonce*/
  sendAction() {
    var ajaxLink = apiLink + 'order/' + this.props.id + '/' + this.props.actionType
    ajax({
    method: 'post',
    url: ajaxLink,
    headers : {
      'X-WP-Nonce' : nonce
    }
    }).then(function(res) {
      console.log(res);
    })
  }
  render() {
    var data = {};

    for (var prop in this.props) {
      if(this.props.hasOwnProperty(prop) && !(prop === 'id' || prop === 'actionType' )) {
        data[prop] = this.props[prop]
      }
    }
    return (
      <RaisedButton {...data} onTouchTap={this.sendAction.bind(this)} />
    );
  }
}

export default OrderButton;



