import React, { Component } from 'react';
import './OrderGroup.css';
import Order from './Order';
import ajax from '@fdaciuk/ajax'
import EmptyTab from './EmptyTab';
import CircularProgress from 'material-ui/CircularProgress';

const styles = {
  progress : {
    marginLeft: 'auto',
    marginRight: 'auto',
    display: 'flex',
    justifyContent: 'center',
    marginTop: '64px'
  }
};
class OrderGroup extends Component {
  constructor(props) {
   super(props);
   this.state = {
     orderData: '',
     orders: <CircularProgress style={styles.progress} />
   };
 }
removeOrder = (orderId) => {
  var orders = this.state.orderData
  delete orders.orderId
  this.setState({
    orderData : orders
  })
}
 loadOrders = () => {
   /*global apiLink*/
   /*global nonce*/
   var type = this.props.groupType
   var ajaxLink = apiLink + 'orders?type=' + type
   ajax({
   method: 'get',
   url: ajaxLink,
   headers: {
     'X-WP-Nonce' : nonce
   }
   }).then(function(res) {
     console.log(Object.keys(res.orders).length);
     this.setState({
       orderData : res.orders,
       orders: <EmptyTab />
     })
   }.bind(this))
 }
  componentDidMount() {
    this.loadOrders()
  }
  render() {
    var orders = this.state.orders
    var order, orderId
    var orderData = this.state.orderData
    var reversedOrderData = Object.keys(orderData).sort().reverse()
    if(Object.keys(orderData).length) {
      orders = []
      for (var i = 0; i < Object.keys(orderData).length; i++) {
          orderId = reversedOrderData[i]
          order = orderData[orderId]
          orders.push(<Order key={orderId} orderId={orderId} orderStatus={order.data.status} order={order.labels}  />)
        }
    }

    return (
      <div className="OrderGroup">
        {orders}
      </div>
    );
  }
}

export default OrderGroup;



