import React, { Component } from 'react';
import './App.css';
import OrderGroup from                './OrderGroup';
import EmptyCoupon from               './EmptyCoupon';
import {Tabs, Tab} from               'material-ui/Tabs';
import AppBar from                    'material-ui/AppBar';
import FlatButton from                'material-ui/FlatButton';
import NavigationArrowBack from       'material-ui/svg-icons/navigation/arrow-back';


const styles = {
  appBar: {
    flexWrap: 'wrap',
    flexDirection: 'column',
    boxShadow: "none"
  },
  tabs: {
    width: '100%',
    boxShadow: "rgba(0, 0, 0, 0.117647) 0px 1px 6px, rgba(0, 0, 0, 0.117647) 0px 1px 4px"
  },
  backButton: {
    color: '#ffffff',
    fill: "#ffffff",
  },
  backButtonContainer : {
    alignSelf: "flex-start"
  },
  title : {
    textAlign: "left",
    marginLeft: 64
  }
};
class App extends Component {
  render() {
    return (
      <div className="App">
        <AppBar
         title="Orders Center"
         style={styles.appBar}
         iconStyleLeft={styles.backButtonContainer}
         titleStyle={styles.title}
         iconElementLeft={ <FlatButton
            label="Back To WordPress"
            href="http://nayeftoys.com/wp-admin"
            style={styles.backButton }
            secondary={true}
            icon={<NavigationArrowBack />}
    />}
       >

    </AppBar>
    <Tabs tabItemContainerStyle={styles.tabs} initialSelectedIndex={0} >
      <Tab label="Orders">
        <Tabs tabItemContainerStyle={styles.tabs} initialSelectedIndex={3}>
          <Tab label="New Orders">
              <OrderGroup groupType='new'></OrderGroup>
          </Tab>
          <Tab label="Paid Orders">
              <OrderGroup groupType='paid'></OrderGroup>
          </Tab>
          <Tab label="Delivered Orders">
              <OrderGroup groupType='delivered'></OrderGroup>
          </Tab>
          <Tab label="All Orders">
            <OrderGroup groupType='all'></OrderGroup>
          </Tab>
        </Tabs>
      </Tab>
      <Tab label="Coupons">
        <EmptyCoupon />
      </Tab>
    </Tabs>

      </div>
    );
  }
}

export default App;



